        methods: {
            initial() {
                this.hidden = false;
                this.showing = true;
                this.shrinking = false;
                clearTimeout(this.timeout);
                this.timeout = setTimeout(() => {
                    this.showing = false;
                }, 2300);
            },
            shrink() {
                this.small = true;
                this.shrinking = true;
                this.showing = false;
                clearTimeout(this.timeout);
                this.timeout = setTimeout(() => {
                    this.shrinking = false;
                }, 2300);
            },
            grow() {
                this.small = false;
                this.shrinking = true;
                this.showing = false;
                clearTimeout(this.timeout);
                this.timeout = setTimeout(() => {
                    this.shrinking = false;
                }, 2300);
            }
        },
        mounted() {
            setTimeout(() => this.initial(), 500);
        }
    });
